<template>
    <div class="fillInForm">
        <el-form :model="formData" :rules="rules" ref="ruleForm" label-width="120px" class="demo-ruleForm" size="small">
            <el-form-item label="" prop="name">
                <el-input  v-model="formData.name"></el-input>
            </el-form-item>
            <el-form-item label="" prop="property">
                <el-select  v-model="formData.property" placeholder="">
                  <el-option label="" :value="0"></el-option>
                  <el-option label="" :value="1"></el-option>
                  <el-option label="" :value="2"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="" prop="trade">
                <el-select  v-model="formData.trade" placeholder="">
                  <el-option :label="item" :value="index+13" v-for="(item,index) in tradeList"></el-option>
                  <!-- <el-option label="" :value="1"></el-option> -->
                </el-select>
            </el-form-item>
            <el-form-item label="" prop="adress">
                  <area-select @change="handlerAdressChange" :level="2" type="text" v-model="formData.adress" :data="pcaa" ></area-select>
            </el-form-item>
            <el-form-item label="" >
                <el-input  v-model="formData.detailedAddress"></el-input>
            </el-form-item>
            <el-form-item label="" prop="contactPerson">
                <el-input  v-model="formData.contactPerson"></el-input>
            </el-form-item>
            <el-form-item label="" prop="contactPhone">
                <el-input  v-model="formData.contactPhone"></el-input>
            </el-form-item>
            <el-form-item label="" prop="employeeScale">
                <el-select  v-model="formData.employeeScale" placeholder="">
                  <el-option label="0~20" :value="0"></el-option>
                  <el-option label="20~100" :value="1"></el-option>
                  <el-option label="100" :value="2"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="" prop="equipmentScale">
                <el-input-number :min="0" :controls="false" style="width:100%;"  v-model="formData.equipmentScale"></el-input-number>
            </el-form-item>
            <el-form-item label="" prop="onIcloudScale">
                <el-input-number :min="0" :controls="false" style="width:100%;" v-model="formData.onIcloudScale"></el-input-number>
            </el-form-item>
            <el-form-item label="" prop="siteImageShareUrl">
                <el-upload
                    class="avatar-uploader"
                    action="http://61.153.187.80:8081/fileSystem/uploadFile"
                    :show-file-list="false"
                    :on-success="uploadSuccess"
                    :before-upload="beforeAvatarUpload">
                    <!-- {{formData.siteImageShareUrl}} -->
                    <img v-if="formData.siteImageShareUrl" :src="formData.siteImageShareUrl" class="avatar">
                    <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                    <div slot="tip" class="el-upload__tip">JPGPNG</div>
                </el-upload>
            </el-form-item>
            <el-form-item>
                <el-checkbox v-model="isRead"> </el-checkbox><span style="margin-left:10px;"></span><span class="protocol"></span>
                <div @click="onSubmit" class="submit-btn" :class="{'isDisabled':!isRead}"></div>
            </el-form-item>
        </el-form>
    </div>
</template>
<script>
import rule from "../../assets/js/rule.js";
import {mapState} from 'vuex'
import { pca, pcaa } from "area-data";
export default {
  data() {
    return {
      tradeList:['','','','','','','','','21','','','','','','','','',
'','','','','','','',''
,'','','','','','' ],
      pcaa: pcaa,
      rules: {
        name: rule.require("string", ""),
        adress: rule.require("array",""),
        property: rule.require("number", ""),
        trade: rule.require("string",""),
        contactPerson: rule.require("string", ""),
        contactPhone: [
          { required: true, message: '', trigger: 'change' },
          { validator: rule.phone, trigger: 'blur' }
        ],
        employeeScale: rule.require("string",""),
        siteImageShareUrl: rule.require("string", ""),
        equipmentScale: rule.require("number", ""),
        onIcloudScale: rule.require("number", ""),
      },
      // 
      isRead: false
    };
  },
  computed:{
    ...mapState(['ports'])
  },
  props: ["formData","operateType"],
  methods: {
    handlerAdressChange(data){
      this.$refs.ruleForm.validateField('adress')
    },
    uploadSuccess(file) {
      console.log(file);
      this.$set(this.formData, "siteImageShareUrl", file.model);
      this.$refs.ruleForm.validateField("siteImageShareUrl");
      console.log(this.formData);
      // this.formData.siteImageShareUrl=file.model
    },
    beforeAvatarUpload(file) {
      const isJPG = file.type === "image/jpeg";
      const isPNG = file.type === "image/png";
      const isLt2M = file.size / 1024 / 1024 < 2;
      console.log(file);
      if (!isJPG && !isPNG) {
        this.$message.error(" JPG  PNG !");
      }
      if (!isLt2M) {
        this.$message.error(" 2MB!");
      }
      return (isJPG || isPNG) && isLt2M;
    },
    onSubmit() {
      if (!this.isRead) {
        return false;
      }
      this.$refs["ruleForm"].validate(valid => {
        if (valid) {
          if(this.operateType=='add'){
            var obj={...this.formData}
            obj.province=obj.adress[0];
            obj.city=obj.adress[1];
            obj.district=obj.adress[2];
            delete obj.adress
            this.$axios.post_noqs(
                this.ports + '/onicloud/add',
                {...obj},
                this
            ).then(res=>{
                if(res.success){
                  console.log(res,'----')
                    this.$emit("onSubmit",res.data);
                }
            })
          }else{
            var obj={};
            obj={
              id:this.formData.id,
              name: this.formData.name,
              province:this.formData.province,
              city:this.formData.city,
              district:this.formData.district,
              property:this.formData.property,
              trade: this.formData.trade,
              contactPerson:this.formData.contactPerson,
              contactPhone:this.formData.contactPhone,
              employeeScale: this.formData.employeeScale,
              siteImageShareUrl: this.formData.siteImageShareUrl,
              equipmentScale:this.formData.equipmentScale,
              detailedAddress:this.formData.detailedAddress,
              onIcloudScale: this.formData.onIcloudScale,
            }
            this.$axios.post_noqs(
                this.ports + '/onicloud/update',
                {...obj},
                this
            ).then(res=>{
                if(res.success){
                  console.log(res,'----')
                    this.$emit("onSubmit",res.data);
                }
            })
          }
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    }
  }
};
</script>
<style lang="less">
.fillInForm {
  margin-top: 0px;
  width: 440px;
  .el-input-number .el-input input{
    text-align: left;
  }
  .submit-btn.isDisabled {
    opacity: 1;
    cursor: not-allowed;
    background: #a0cfff;
  }
  .protocol {
    cursor: pointer;
    font-size: 14px;
    font-family: MicrosoftYaHei;
    margin-left: 10px;
    color: #0d6ff4;
  }
  .el-form-item__label {
    padding: 0 20px 0 0;
  }
  .submit-btn {
    width: 220px;
    height: 44px;
    background: rgba(13, 111, 244, 1);
    font-size: 14px;
    font-family: MicrosoftYaHei;
    color: rgba(255, 255, 255, 1);
    line-height: 44px;
    text-align: center;
    cursor: pointer;
    margin-top: 30px;
  }
  .avatar-uploader {
    margin-top: 4px;
  }
  .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409eff;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 240px;
    height: 140px;
    line-height: 140px;
    text-align: center;
  }
  .avatar {
    width: 240px;
    height: 140px;
    display: block;
  }
  .el-upload__tip {
    margin-top: -10px;
  }
}
</style>



