      /* toastData:{
          title:'',//
          dialogVisible:true,//
          width:'600px',//
          btnData:[{
            name:'',//
            method:'dialogSuer',//
            bgColor:'dialog-bule'//
          },{
            name:'',
            method:'savaAgain',
            bgColor:'dialog-deep-blue'
          }]
      }*/
<template>
    <el-dialog class="poc-toast-copy" :title="config.title" :visible.sync="config.dialogVisible" :close-on-click-modal="false" :append-to-body="config.appendToBody" :width="config.width" @close="callBack('handleClose')">
        <!--    -->
        <slot></slot>
        <!--  -->
        <div class="btn-group">
            <el-button class="dialog-white" v-for="item in config.btnData" v-show="item.isShow" :disabled="item.disabled" :class="item.bgColor" @click="callBack(item.method)">{{item.name}}</el-button>
        </div>
    </el-dialog>
</template>
<script>
export default {
  props: ["config"],
  methods: {
    callBack(method) {
      this.$emit(method);
    }
  }
};
</script>
<style lang="less">
// @headerbgImage: linear-gradient(to right, #4a8dd2, #2acaff);
@headerbgColor:#F0EFF5;
@header-height:40px;
.poc-toast-copy {
  .el-dialog {
    border-radius: 0px;
    overflow: hidden;
  }
  .el-dialog__header {
    background-color: @headerbgColor;  
    position: relative;
    height: @header-height;
    line-height: @header-height;
    padding: 0 10px;

    .el-dialog__title {
      color: #333333;
    }
  }
  .el-dialog__body {
    padding: 25px 0 0 0 !important;
  }
  .my-btn-group {
    text-align: center;
    // background-color: #eef1f5;
    padding: 5px 10px;
    margin-top: 20px;
    button{
      padding: 8px 35px;
    }
    margin-bottom: 20px;
  }
  .el-dialog__headerbtn{
    color: #333333;
    position: absolute;
    top:50%;
    transform: translate(0,-50%)
  }
}
</style>



