import axios from 'axios'
import qs from 'qs'
axios.defaults.withCredentials=true;
axios.interceptors.request.use(config => {    // config
    // localStorageapi_token
    if (localStorage.getItem('api_token')) {
        //  api_token request header
        config.headers.apiToken = `${localStorage.getItem('api_token')}`;
    }
    return config;
}, err => {
    return Promise.reject(err);
});

axios.interceptors.response.use(response => {
    return response
}, error => {
    return Promise.resolve(error.response)
});

function checkStatus (response) {
    // http
    if (response && (response.status === 200 || response.status === 304 ||
            response.status === 400)) {
        return response
    }
    // 
    return {
        status: -404,
        msg: ''
    }
}

function checkCode (res,self) {
    // code()
    // if (res.status === -404) {
    //     self.$router.push('/404')
    //     return false
    // }
    console.log(res,'======')
    if (res.data) {
        if(res.data.result){
            return {success:true,data:res.data.model}
        }else{
            self.$alert(res.data.message, '', {
                confirmButtonText: ''
            });
            return {success:false,data:res.data.message}
        }
    }
    return res
}
// 
export default {
    post (url, data,self) {  //  post
        return axios({
            method: 'post',
            baseURL: '/backapis',
            url,
            data: qs.stringify(data),
            timeout: 20000,
        }).then(
            (response) => {
                return checkStatus(response)
            }
        ).then(
            (res) => {
                return checkCode(res,self)
            }
        )
    },
    post_noqs (url, data,self) {  //  post
        return axios({
            method: 'post',
            baseURL: '/backapis',
            url,
            data: data,
            timeout: 20000,
        }).then(
            (response) => {
                return checkStatus(response)
            }
        ).then(
            (res) => {
                return checkCode(res,self)
            }
        )
    },
    get (url, params,self) {  // get
        return axios({
            method: 'get',
            baseURL: '/backapis',
            url,
            params, // get 
            timeout: 20000,
        }).then(
            (response) => {
                return checkStatus(response)
            }
        ).then(
            (res) => {
                return checkCode(res,self)
            }
        )
    }
}


