
import http from "./public"

// 
export const personalAuthAdd= (data) => {
	return http.fetchPostJson('/personalAuth/add',data)
}
// 
export const personalAuthPage= (data) => {
	return http.fetchPostJson('/personalAuth/page',data)
}
// 
export const personalAuthUpdate= (data) => {
	return http.fetchPostJson('/personalAuth/update',data)
}
// 
export const enterpriseauthAdd= (data) => {
	return http.fetchPostJson('/enterpriseauth/add',data)
}
// 
export const enterpriseauthPage= (data) => {
	return http.fetchPostJson('/enterpriseauth/page',data)
}
// 
export const enterpriseauthUpdate= (data) => {
	return http.fetchPostJson('/enterpriseauth/update',data)
}





