:fixed="item.fixed" // :prop="item.field" // :width="item.width" // :sortable="item.isSort" //
:resizable="item.isResize" // :show-overflow-tooltip="true" //tooltip :label="item.label" //
:align="tableConfig.align" // :disabled="item.disabled"//
<template>
  <div class="myTable">
    <!--  -->
    <toolbar v-if="tableConfig.toolbarConfig" @toolbarBack="toolbarBack" :toolbarConfig="tableConfig.toolbarConfig">
      <slot></slot>
    </toolbar>
    <!--  -->
    <el-table style="margin-top: 5px" :data="tableData" @select="select" @select-all="selectAll" @row-dblclick="rowDblclick"  :height="tableConfig.height?tableConfig.height:560"
      :highlight-current-row="tableConfig.isHigh">
      <!--  -->
      <el-table-column v-if="tableConfig.isSelection" :selectable="selectable" type="selection" width="50" :align="tableConfig.align"
        :disabled="tableData.length<0">
      </el-table-column>
      <el-table-column type="index" label="" v-if="tableConfig.isOrder" width="50"></el-table-column>
      <!--  -->
      <el-table-column v-for="(item,index,$index) in tableConfig.colConfig" :fixed="item.fixed" :prop="item.field" :width="item.width"
        :sortable="item.isSort" :resizable="item.isResize" :show-overflow-tooltip="!item.noTooltip" :label="item.label" :align="tableConfig.align">
        <!--  -->
        <template slot-scope="scope">
          <!--  -->
          <span v-if="item.type === 'text'">{{formatter(item.field,scope.row[item.field],scope.row)}}</span>
          <!--  -->
          <el-input v-else-if="item.type === 'input'" @blur="leaveBlur(scope.$index,scope.row[item.field],index)" v-model="scope.row[item.field]"
            :disabled="item.disabled" size="mini"></el-input>
          <!--  -->
          <el-input-number v-else-if="item.type === 'inputNumber'" v-model="scope.row[item.field]" @change="inputNumberChange(scope.$index,scope.row[item.field])"
            :step="item.step" :min="scope.row['minTax']" :max="scope.row['maxTax']" style="width:100%" size="mini"></el-input-number>
          <!---->
          <el-input-number v-else-if="item.type === 'inputNum'" v-model="scope.row[item.field]" @change="inputNumChange(scope.$index,scope.row[item.field])"
            :step="item.step" :min="0" style="width:100%" size="mini"></el-input-number>
          <!--  -->
          <el-select v-else-if="item.type === 'select'" @change="filterChange(scope.$index,scope.row[item.field],index)" v-model="scope.row[item.field]"
            clearable placeholder="" size="mini">
            <el-option v-for="_item in scope.row['select_'+item.field]" :key="_item.value" :label="_item.label" :value="_item.value">
            </el-option>
          </el-select>
          <!--  -->
          <el-select v-else-if="item.type === 'selects'" v-for="list in item.lists" :key="list.field" v-model="scope.row[list.field]"
            :placeholder=list.placeholder size="mini" @change=list.method(scope.row[list.field],scope.row)>
            <el-option v-for="_item in list.options" :key="_item.value" :label="_item.label" :value="_item.value">
            </el-option>
          </el-select>
          <!--  -->
          <el-select v-else-if="item.type === 'filterSelect'" filterable @change="filterChange(scope.$index,scope.row[item.field],index)"
            v-model="scope.row[item.field]" clearable placeholder="" size="mini" @focus="selectFocus(scope.$index,index)">
            <el-option v-for="_item in scope.row['select_'+item.field]" :key="_item.value" :label="_item.label" :value="_item.value">
            </el-option>
          </el-select>
          <!--  -->
          <el-date-picker v-model="scope.row[item.field]" v-else-if="item.type === 'date'" placeholder="" size="mini" style="width:100%"></el-date-picker>
          <!--  -->
          <el-date-picker v-model="scope.row[item.field]" v-else-if="item.type === 'dateTime'" placeholder="" :picker-options="pickerOptions"
            size="mini" style="width:100%"></el-date-picker>
          <!--  -->
          <el-button v-else-if="item.type === 'btnText'" type="text" size="mini" @click=" buttonFunction(scope.row,scope.$index) ">{{item.field}}</el-button>
          <div v-else-if="item.type === 'btnsText'">
            <el-button type="text" v-for="it in item.fields" @click="callBack(it.method,scope.row,scope.$index)" size="mini">
              <!--<span v-if=""></span>-->
              {{it.name}}
            </el-button>
          </div>
          <!---->
           <div v-else-if="item.type === 'btnsApprove'">
            <el-button type="text" size="mini" @click=" buttonFunction(scope.row,scope.$index,'') "></el-button>
            <el-button :disabled="scope.row.statusDesc == '' || scope.row.proStatus == ''||scope.row.statusDesc == '' || scope.row.proStatus == ''||scope.row.proStatus == ''||scope.row.statusDesc == '' || scope.row.proStatus == ''"
              type="text" size="mini" @click=" buttonFunction(scope.row,scope.$index,'') "></el-button>
          </div>
          <!---->
          <div v-else-if="item.type === 'btnsSure'">
            <el-button v-if="scope.row.processState == ''" type="text" size="mini" @click=" buttonFunction(scope.row,scope.$index) ">{{item.fields}}</el-button>
            <el-button v-if="scope.row.processState == ''" type="text" size="mini"></el-button>
            <span v-else-if="scope.row.processState == ''">{{scope.row.payName?'':''}}</span>
            <span v-else-if="scope.row.processState == ''"></span>
          </div>
          <!--  -->
          <el-checkbox v-else-if="item.type === 'checkbox'" v-model="scope.row[item.field]" :disabled="item.disabled">{{item.label}}</el-checkbox>

          <!---->
          <div v-else-if="item.type === 'btnsApproves'">
            <el-button type="text" @click="approve(scope.row,scope.$index)" size="mini">
              <span v-if="scope.row.trouStatus == 'ACS001'"></span>
              <span v-else></span>
            </el-button>
            <el-button type="text" @click="processRecord(scope.row,scope.$index)"></el-button>
          </div>
          <!--  -->
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <div class="my-pagination">
      <el-pagination v-if="tableConfig.isPage" @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="tableConfig.pageNo"
        :page-sizes="[20, 40, 60, 80]" :page-size="tableConfig.pageSize" layout="total, sizes, prev, pager, next, jumper" :total="tableConfig.total">
      </el-pagination>
    </div>
    <!-- loading -->
    <loading v-show="tableConfig.loadShow"></loading>
  </div>
</template>


<script>
  export default {
    props: ["tableConfig", "tableData"],
    data() {
      return {
        pickerOptions: {
          disabledDate(time) {
            return time.getTime() < Date.now();
          }
        }

        // tableConfig:{
        //   toolbarConfig:[{//
        //           loading:true,
        //           icon:'xxx',
        //           disabled:true,
        //           method:'xxxx',
        //           name:''
        //     }],
        //     //
        //     colConfig:[ { field: "voucherId", label: "", type: "input"},
        //                 { field: "inDate", label: "", type: "select"},
        //                 { field: "empName", label: "", type: "text" },
        //                 { field: "remark", label: "", type: "text" }],
        //     isSelection:true,//
        //     isPage:true,//
        //     isFormatter:false,
        //     currentSelectArr:[],//
        //     align:'center',//
        //     pageNo:1,
        //     pageSize:20,
        //     total:100,
        //     isHigh:false,
        //     isLoading:false,//loading
        //     loadShow:false,//loading
        //      },
        //     tableData:[{voucherId:'xxxx',inDate:'20152520',empName:'xxxxas',remark:'xxxasda',selectData:[{value:'xxx0',label:'xxx'},{value:'xxx0',label:'xxx'}]},
        //                {voucherId:'xxxx',inDate:'20152520',empName:'xxxxas',remark:'xxxasda'},{voucherId:'xxxx',inDate:'20152520',empName:'xxxxas',remark:'xxxasda'}]
      };
    },
    methods: {
      callBack(method, val, index) {
        this.$emit(method, val, index);
      },
      // 

      formatter(field, cellValue, row) {
        if (this.tableConfig.isFormatter) {
          let val = null;
          this.$emit(
            "formatter",
            field,
            cellValue,
            value => {
              val = value;
            },
            row
          );
          return val;
        } else {
          return cellValue;
        }
      },
      toolbarBack(data) {
        //
        this.$emit(data.method, data.index);
      },
      handleSizeChange(val) {
        //
        if (this.tableConfig.isLoading) {
          this.tableConfig.loadShow = true; //
        }
        this.tableConfig.pageSize = val;
        this.$emit("sizeChange");
      },
      handleCurrentChange(val) {
        //
        if (this.tableConfig.isLoading) {
          this.tableConfig.loadShow = true; //
        }
        this.tableConfig.pageNo = val;
        this.$emit("pageChange");
      },
      select(val) {
        //Checkbox
        this.tableConfig.currentSelectArr = val;
      },
      selectAll(val) {
        //Checkbox
        this.tableConfig.currentSelectArr = val;
      },
      selectable(row, index) {
        if (this.tableConfig.selectable) {
          if (row[this.tableConfig.disabledField] == this.tableConfig.field) {
            return false;
          } else {
            return true;
          }
        } else {
          return true;
        }
      },
      rowDblclick(val) {
        //
        this.$emit("rowDblclick", val);
      },
      buttonFunction(row, index, type) {
        //
        this.$emit("buttonFunction", row, index, type);
      },
      filterChange(index, val, $index) {
        //
        this.$emit("filterChange", index, val, $index);
      },
      leaveBlur(index, val, $index) {
        //
        this.$emit("leaveBlur", index, val, $index);
      },
      inputNumberChange(index, val) {
        //
        this.$emit("inputNumberChange", index, val);
      },
      selectFocus(index, $index) {
        if ($index != 0) {
          this.$emit("selectFocus", index, $index);
        }
      },
      inputNumChange(index, val) {
        //
        this.$emit("inputNumChange", index, val);
      },
      //
      approve(index, val) {
        this.$emit("approve", index, val);
      },
      //
      processRecord(index, val) {
        this.$emit("processRecord", index, val);
      }
    },
    watch: {
      tableData: function () {
        this.tableConfig.currentSelectArr = [];
      }
    }
  };

</script>
<style lang="less">
  @colPading: 8px 0;
  @theadColor: #333333;
  @theadImage: linear-gradient(to right, #dee3e8, #dee3e8);
  .myTable .el-button--text:focus,
  .myTable .el-button--text:hover {
    color: #c0c4cc;
  }

  .myTable {
    position: relative;

    .my-pagination {
      text-align: center;
      margin-top: 10px;
    }
    .el-table td {
      padding: @colPading;
    }
    .el-table thead {
      color: @theadColor;
      font-weight: 500;
      background-image: @theadImage; // background-color: #DEE3E8;
    }
    .el-table th,
    .el-table tr {
      background-color: #F5F5F5;
    }
  }

</style>



